/**
 * External dependencies
 */
import { v4 as uuid } from 'uuid';
import type {
	ConversionEvent,
	ExperimentId,
	Maybe,
	TrackEvent,
	VisitEvent,
} from '@nab/types';

/**
 * Internal dependencies
 */
import { getCookie } from '../../utils/cookies';
import { addQueryArgs } from '../../utils/url';
import {
	canSynchEvent,
	isStagingSite,
	updateExperimentsWithPageViews,
} from '../../utils/tracking';
import { getActiveSegments } from '../../utils/segments';

import type {
	ConvertingGoal,
	GoalIndex,
	HeatmapSummary,
	Settings,
} from './types';

export function sendViews(
	expIds: ExperimentId | ReadonlyArray< ExperimentId >,
	settings: Settings
): void {
	const isVisitEvent = ( e?: VisitEvent ): e is VisitEvent => !! e;

	const events = ( Array.isArray( expIds ) ? expIds : [ expIds ] )
		.map( ( id ) => generatePageViewEvent( id, settings ) )
		.filter( isVisitEvent );

	track( events, settings );
} //end trackPageViews()

export function sendConversions(
	convGoals: ConvertingGoal | ReadonlyArray< ConvertingGoal >,
	settings: Settings
): void {
	const { experiments } = settings;
	const isConvEvent = ( e?: ConversionEvent ): e is ConversionEvent => !! e;
	const events = ( Array.isArray( convGoals ) ? convGoals : [ convGoals ] )
		.map( ( { experiment, goal } ) =>
			generateConversionEvent( experiment, goal, experiments )
		)
		.filter( isConvEvent );

	track( events, settings );
} //end sendConversions()

export function track(
	events: ReadonlyArray< TrackEvent >,
	settings: Settings
): void {
	updateExperimentsWithPageViews( events, settings );

	events = events.filter( canSynchEvent );
	if ( ! events.length ) {
		return;
	} //end if

	if ( isStagingSite() ) {
		events.forEach( ( event ) => console.info( '[Staging] Event', event ) ); // eslint-disable-line
		return;
	} //end if

	const { siteId, trackingUrl, timezone } = settings;
	const timestamp = new Date().toISOString();
	events = events.map( ( event ) => ( {
		...event,
		id: uuid(),
		timezone,
		timestamp,
	} ) );

	const url = addQueryArgs( trackingUrl, {
		e: window.btoa( JSON.stringify( events ) ),
		a: siteId,
	} );

	/* eslint-disable */
	const beaconSent =
		!! settings.useSendBeacon &&
		'function' === typeof window.navigator.sendBeacon &&
		!! window.Blob &&
		window.navigator.sendBeacon(
			url,
			new Blob( [], { 'Content-Type': 'text/plain' } as any )
		);
	/* eslint-enable */

	if ( ! beaconSent ) {
		const image = document.createElement( 'img' );
		image.setAttribute( 'src', url );
	} //end if
} //end track()

function generatePageViewEvent(
	experimentId: ExperimentId,
	settings: Settings
): Maybe< VisitEvent > {
	const { experiments, heatmapTracking } = settings;

	const experiment = experiments[ experimentId ];
	if ( ! experiment ) {
		return maybeGeneratePageViewEventForHeatmap(
			experimentId,
			heatmapTracking
		);
	} //end if

	const val = Number.parseInt( getCookie( 'nabAlternative' ) ?? '' );
	const alternativeCookie = isNaN( val ) ? -1 : val;
	if ( -1 === alternativeCookie ) {
		return;
	} //end if

	const segments = getActiveSegments( experimentId );
	if ( ! segments.length ) {
		return;
	} //end if

	const alternative = alternativeCookie % experiment.alternatives.length;

	return {
		kind: 'visit',
		experiment: experimentId,
		alternative,
		segments,
	};
} //end generatePageViewEvent()

function maybeGeneratePageViewEventForHeatmap(
	experimentId: ExperimentId,
	heatmapTracking: ReadonlyArray< HeatmapSummary >
): Maybe< VisitEvent > {
	const ids = heatmapTracking.map( ( { id } ) => id );
	if ( ! ids.includes( experimentId ) ) {
		return;
	} //end if

	return {
		kind: 'visit',
		experiment: experimentId,
		alternative: 0,
		segments: [],
	};
} //end maybeGeneratePageViewEventForHeatmap()

function generateConversionEvent(
	experimentId: ExperimentId,
	goalIndex: GoalIndex,
	experiments: Settings[ 'experiments' ]
): Maybe< ConversionEvent > {
	const experiment = experiments[ experimentId ];
	if ( ! experiment ) {
		return;
	} //end if

	if ( experiment.goals.length <= goalIndex ) {
		return;
	} //end if

	const val = Number.parseInt( getCookie( 'nabAlternative' ) ?? '' );
	const alternativeCookie = isNaN( val ) ? -1 : val;
	if ( undefined === alternativeCookie ) {
		return;
	} //end if

	const segments = getActiveSegments( experimentId );
	if ( ! segments.length ) {
		return;
	} //end if

	const alternative = alternativeCookie % experiment.alternatives.length;
	return {
		kind: 'conversion',
		experiment: experimentId,
		alternative,
		goal: goalIndex,
		segments,
	};
} //end generateConversionEvent()
