/**
 * Internal dependencies
 */
import domReady from '../../utils/dom-ready';
import { sendConversions } from './sync';
import { getAllActions, getConvertingGoals } from './utils';
import type { ConvertibleAction, Settings } from './types';

export function initConversionTracking( settings: Settings ): void {
	domReady( () => addActionListeners( settings ) );
	domReady( () => sendConvertingPageViews( settings ) );
} //end initConversionTracking()

type ActionListener = ( action: ConvertibleAction ) => false | void;
const actionListeners: ActionListener[] = [];
export function addActionTypeListeners( al: ActionListener ): void {
	actionListeners.push( al );
} //end addActionTypeListeners()

// =======
// HELPERS
// =======

function addActionListeners( settings: Settings ) {
	getAllActions( settings.experiments )
		.filter( ( action ) => 'nab/page-view' !== action.type )
		.forEach( ( action ) =>
			actionListeners.forEach( ( addListeners ) =>
				addListeners( action )
			)
		);
} //end addActionListeners()

function sendConvertingPageViews( settings: Settings ) {
	const { alternativePostIds, experiments } = settings;

	const events = getConvertingGoals(
		experiments,
		( action ) =>
			'nab/page-view' === action.type &&
			alternativePostIds.includes( action.attributes.postId as number )
	);
	sendConversions( events, settings );
} //end sendConvertingPageViews()
