/**
 * External dependencies
 */
import 'es6-symbol';
import type { ExperimentId } from '@nab/types';

/**
 * Internal dependencies
 */
import { initConversionTracking } from './conversions';
import { initHeatmapTracking } from './heatmaps';
import { initDefaultListeners } from './listeners';
import { sendConversions, sendViews } from './sync';

import { cleanOldSegments } from '../../utils/segments';
import { doesVisitorParticipate } from '../../utils/participation';
import { addSingleAction, doSingleAction } from '../../utils/actions';

import type { ConvertingGoal, GoalIndex, Settings } from './types';

type PendingEvent =
	| { type: 'view'; experimentIds: ReadonlyArray< ExperimentId > }
	| { type: 'trigger'; eventName: string }
	| { type: 'convert'; experiment: ExperimentId; goal: GoalIndex };

let pendingEvents: ReadonlyArray< PendingEvent > = [];
let settings: Settings;
export function init( initialSettings: Settings ): void {
	if ( ! initialSettings ) {
		return;
	} //end if

	if ( settings ) {
		return;
	} //end if
	settings = initialSettings;

	if ( ! doesVisitorParticipate( settings ) ) {
		return;
	} //end if

	const experimentIds = Object.keys( settings.experiments )
		.map( ( id ) => Number.parseInt( id ) )
		.filter( ( id ) => ! isNaN( id ) && 0 < id );
	cleanOldSegments( experimentIds );

	addSingleAction( 'main-ready', () => {
		initDefaultListeners( convert );
		initConversionTracking( settings );
		initHeatmapTracking( settings );
	} );

	doSingleAction( 'main-ready' );
} //end init()

export function view(
	expIds: ExperimentId | ReadonlyArray< ExperimentId >
): void {
	const experimentIds = Array.isArray( expIds ) ? expIds : [ expIds ];
	if ( ! experimentIds.length ) {
		return;
	} //end if

	if ( ! settings ) {
		pendingEvents = [ ...pendingEvents, { type: 'view', experimentIds } ];
		return;
	} //end if

	if ( ! doesVisitorParticipate( settings ) ) {
		return;
	} //end if

	sendViews( experimentIds, settings );
} //end view()

export function trigger( eventName: string ): void {
	if ( ! settings ) {
		pendingEvents = [ ...pendingEvents, { type: 'trigger', eventName } ];
		return;
	} //end if

	if ( ! doesVisitorParticipate( settings ) ) {
		return;
	} //end if

	const experimentIds = Object.keys( settings.experiments )
		.map( ( id ) => Number.parseInt( id ) )
		.filter( ( id ) => ! Number.isNaN( id ) && 0 < id );
	const { experiments } = settings;
	const events = experimentIds.reduce( ( memo, id ) => {
		const experiment = experiments[ id ];
		experiment.goals.forEach( ( goal, index ) => {
			const hasCustomEventAction = goal.conversionActions.reduce(
				( found, { type, attributes = {} } ) =>
					found ||
					( 'nab/custom-event' === type &&
						eventName === attributes.eventName ),
				false
			);
			if ( hasCustomEventAction ) {
				memo = [ ...memo, { experiment: id, goal: index } ];
			} //end if
		} );
		return memo;
	}, [] as ReadonlyArray< ConvertingGoal > );

	sendConversions( events, settings );
} //end trigger()

export function convert(
	experiment: ExperimentId | ReadonlyArray< ConvertingGoal >,
	goal?: GoalIndex
): void {
	const isExperimentId = (
		e: ExperimentId | ReadonlyArray< ConvertingGoal >
	): e is ExperimentId => 'number' === typeof e;

	let events: ReadonlyArray< ConvertingGoal > = [];
	if ( isExperimentId( experiment ) ) {
		if ( undefined !== goal ) {
			events = [ { experiment, goal } ];
		} //end if
	} else {
		events = experiment;
	} //end if

	if ( ! settings ) {
		pendingEvents = [
			...pendingEvents,
			...events.map( ( ev ) => ( {
				type: 'convert' as const,
				...ev,
			} ) ),
		];
		return;
	} //end if

	if ( ! doesVisitorParticipate( settings ) ) {
		return;
	} //end if

	sendConversions(
		events.map( ( ev ) => ( {
			experiment: ev.experiment,
			goal: ev.goal,
		} ) ),
		settings
	);
} //end convert()

export function debug(): Settings {
	return settings;
} //end debug()

addSingleAction( 'main-ready', () => {
	pendingEvents.forEach( ( event ) => {
		switch ( event.type ) {
			case 'view':
				return view( event.experimentIds );
			case 'trigger':
				return trigger( event.eventName );
			case 'convert':
				return convert( event.experiment, event.goal );
		} //end switch
	} );
	pendingEvents = [];
} );

addSingleAction( 'valid-content', () => {
	doSingleAction( 'main-init-available' );
} );
